import React from 'react';
import { graphql } from 'gatsby';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import ArrowBackIcon from '@material-ui/icons/ArrowBackSharp';
import FileCopySharpIcon from '@material-ui/icons/FileCopySharp';
import Grid from '@material-ui/core/Grid';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import FilledInput from '@material-ui/core/FilledInput';
import { Link } from '@reach/router';

import { Layout, Wrapper, Arrow, CardHeader } from '../components';
import Drama from '../models/Drama';
import dramaDefault from '../utils/drama';
import generatePinkx from '../utils/code';
import Theme from '../models/theme';
import Helmet from 'react-helmet';

const HTMLTitle = '挑选主题';

interface Props {
  data: {
    allThemeYaml: any;
  };
}

interface States {
  drama: Drama;
  theme: Theme;
  allThemeYaml: any;
  perview: string;
  title: string;
}

export default class Preview extends React.Component<Props, States> {
  constructor(props: any) {
    super(props);
    let drama: Drama = dramaDefault;
    let theme: Theme = this.props.data.allThemeYaml.edges[0].node;
    const allTheme: any = this.props.data.allThemeYaml.edges;
    if (typeof window !== `undefined`) {
      const dramaJson: any = window.localStorage.getItem('drama');
      const themeJson: any = window.localStorage.getItem('theme');
      if (dramaJson) {
        drama = JSON.parse(dramaJson);
      }
      if (themeJson) {
        theme = JSON.parse(themeJson);
      }
    }
    this.state = {
      drama,
      theme,
      allThemeYaml: allTheme,
      perview: this.generatePreview(drama, theme),
      title: HTMLTitle,
    };
    this.handleTextFiledChange = this.handleTextFiledChange.bind(this);
    this.handleSelectChange = this.handleSelectChange.bind(this);
    this.handleButtonClick = this.handleButtonClick.bind(this);
  }

  handleSelectChange(event: any) {
    const target = event.target;
    const value: any = target.value;
    const theme: Theme = this.props.data.allThemeYaml.edges[value].node;
    const drama: Drama = this.state.drama;
    this.setState({ theme, perview: this.generatePreview(drama, theme) });
    if (typeof window !== `undefined`) {
      window.localStorage.setItem('theme', JSON.stringify(theme));
    }
  }

  handleTextFiledChange(event: any) {
    const target = event.target;
    const value: any = target.value;
    this.setState({ perview: value });
  }

  handleButtonClick() {
    const e: any = document.getElementById('code');
    if (e) {
      e.select();
      document.execCommand('Copy');
    }
  }

  generatePreview(drama: Drama, theme: Theme) {
    return generatePinkx(drama, theme)
      .replace(/(\s\s)*/g, '')
      .replace(/\n*/g, '');
  }

  public render() {
    const { perview, title, theme, allThemeYaml } = this.state;
    console.log(allThemeYaml);
    return (
      <Layout>
        <Helmet title={title} />
        <Wrapper>
          <Card>
            <CardHeader>
              <h2>{title}</h2>
            </CardHeader>
            <CardContent>
              <Grid container>
                <Grid item xs={6}>
                  <img src={theme.cover} />
                </Grid>
                <Grid item xs={6}>
                  <FormControl fullWidth variant="filled">
                    <Select
                      autoWidth
                      native
                      value={theme.number}
                      onChange={this.handleSelectChange}
                      input={<FilledInput name="age" id="filled-age-native-simple" />}
                    >
                      {allThemeYaml.map((t: any) => {
                        console.log(t.node);
                        return (
                          <option value={t.node.number} key={t.node.number}>
                            {t.node.name} by {t.node.author.name}
                          </option>
                        );
                      })}
                    </Select>
                  </FormControl>
                  <Grid container>
                    <Grid item xs={3}>
                      标签：
                    </Grid>
                    <Grid item xs={9}>
                      {!!theme.tags && theme.tags.join('、')}
                    </Grid>
                  </Grid>
                  <a href={`/theme/${theme.id}`} target="_blank">
                    <Button>预览</Button>
                  </a>
                </Grid>
              </Grid>
            </CardContent>
            <CardContent>
              <TextField
                id="code"
                fullWidth={true}
                variant="filled"
                label="你的代码"
                name="edStr"
                value={perview}
                multiline
                rows={4}
                rowsMax={9}
                onChange={this.handleTextFiledChange}
              />
            </CardContent>
            <Arrow>
              <Button size="large" className={`button`} variant="contained" onClick={this.handleButtonClick} color="primary">
                <FileCopySharpIcon />
                复制代码
              </Button>
              <Link to="/generate">
                <Button size="large" className={`button`}>
                  <ArrowBackIcon />
                  上一步
                </Button>
              </Link>
            </Arrow>
          </Card>
        </Wrapper>
      </Layout>
    );
  }
}

export const GetThemeDefault = graphql`
  query {
    allThemeYaml(sort: { order: DESC, fields: [created] }) {
      edges {
        node {
          author {
            name
          }
          id
          name
          tags
          cover
          css
          number
        }
      }
    }
  }
`;
